import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css'; 
import './Letter.css';
import pictureapple from './images/apple.png';
import axios from 'axios';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom'; 

function Letter() {
  const [startDate, setStartDate] = useState(new Date()); //   
  const [title, setTitle] = useState('');
  const [recipient, setRecipient] = useState('');
  const [sender, setSender] = useState('');
  const [content, setContent] = useState('');
  const [isPopupVisible, setIsPopupVisible] = useState(false);
  const navigate = useNavigate(); 

  //   6   
  const today = new Date();
  const maxDate = new Date();
  maxDate.setMonth(today.getMonth() + 6);

  const handleSubmit = () => {
    if (!title || !recipient || !sender || !content) {
      Swal.fire({
        icon: "warning",
        title: " ",
        text: "  !",
      });
      return;
    }
    setIsPopupVisible(true);
  };

  const confirmSubmission = () => {
    const postData = {
      title, recipient, sender, content,
      date: startDate.toISOString().split('T')[0]
    };
    axios.post('/mailbox/', postData, {
      headers: {
        'Authorization': `Bearer ${localStorage.getItem('access_token')}`,
        'Content-Type': 'application/json'
      }
    })
    .then((response) => {
      if (response.status === 201) {
        Swal.fire({
          icon: "success",
          title: " ",
          text: " !",
        });
        setIsPopupVisible(false);
        setTitle('');
        setRecipient('');
        setSender('');
        setContent('');
        navigate(`/ground/${localStorage.getItem("id")}`); 
      }
    })
    .catch((error) => {
      console.error('    :', error);
      Swal.fire({
        icon: "warning",
        title: " ",
        text: "  .",
      });    
    });
  };

  const cancelSubmission = () => {
    setIsPopupVisible(false);
  };

  return (
    <div className='white-line'>
      <div className='gray'>
        <div className='yellow'>
          <div className='hangs'>
            <p style={{ color: "black", fontSize: '20px' }}></p>
            <div style={{ width: '1vw' }}></div>
            <input className='input-title' type='text' placeholder=' .' value={title} onChange={(e) => setTitle(e.target.value)}/>
          </div>
          <div style={{ height: '4vh' }}></div>
          <div className='hangs'>
            <p style={{ color: "black", fontSize: '20px' }}> </p>
            <div style={{ width: '1vw' }}></div>
            <input className='input-title' type='text' placeholder='   .' value={recipient} onChange={(e) => setRecipient(e.target.value)}/>
          </div>
          <div style={{ height: '4vh' }}></div>
          <div className='hangs'>
            <p style={{ color: "black", fontSize: '20px' }}> </p>
            <div style={{ width: '1vw' }}></div>
            <input className='input-title' type='text' placeholder='  .' value={sender} onChange={(e) => setSender(e.target.value)}/>
          </div>
          <div style={{ height: '4vh' }}></div>
          <div className='hangs'>
            <p style={{ color: "black", fontSize: '20px' }}></p>
            <div style={{ width: '1vw' }}></div>
            <DatePicker
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              className='input-date'
              dateFormat='yyyy/MM/dd'
              minDate={today} //    
              maxDate={maxDate} //   6   
            />
          </div>
          <div style={{ height: '4vh' }}></div>
          <div className='hangs'>
            <p style={{ color: "black", fontSize: '20px' }}></p>
            <div style={{ width: '1vw' }}></div>
            <textarea className='input-content' placeholder=' .' value={content} onChange={(e) => setContent(e.target.value)}/>
          </div>
          <div className='hang-apple'>
            <button className='submit-button' onClick={handleSubmit}></button>
            <img src={pictureapple} width='45vw' height='35vh' alt="Apple" className="apple-image" />
          </div>
        </div>
      </div>
      {isPopupVisible && (
        <div className='letter-popup' onClick={cancelSubmission}>
          <div className='letter-popup-content' onClick={(e) => e.stopPropagation()} style={{ padding: '50px' }}>
            <div className='hangs'>
              <p style={{ paddingRight: '10px', color: "black", fontSize: '20px', fontWeight: 'bold' }}> </p>
              <input className='letter-popup-input' type='text' value={recipient} readOnly/>
            </div>
            <div style={{ height: '5vh' }}></div>
            <div className='hangs'>
              <p style={{ paddingRight: '10px', color: "black", fontSize: '20px', fontWeight: 'bold' }}> </p>
              <input className='letter-popup-input' type='text' value={sender} readOnly/>
            </div>
            <div style={{ height: '5vh' }}></div>
            <p className='letter-popup-message'>?</p>
            <div className='letter-popup-buttons'>
              <button className='letter-popup-button confirm' onClick={confirmSubmission}></button>
              <button className='letter-popup-button cancel' onClick={cancelSubmission}></button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Letter;
